

// 
export const blockDragEffect = (DOM) => {
  const dropzoneId = 'dropzone';

  function loopAncestorNode(dom, id, n) {
    let result = false;
    let counter = 0;
    let currentNode = dom;

    while (counter < n) {
      counter++;
      // 
      if (!currentNode) break;

      // IDture
      if (currentNode.id === id) {
        result = true;
        break;
      } else {
        currentNode = currentNode.parentElement;
      }
    }
    return result;
  }

  function attachEvent(e) {
    const draggable = loopAncestorNode(e.target, dropzoneId, 10);
    if (!draggable) {
      e.preventDefault();
      e.dataTransfer.effectAllowed = 'none';
      e.dataTransfer.dropEffect = 'none';
    }
  }

  DOM.addEventListener('dragenter', attachEvent, false);
  DOM.addEventListener('dragover', attachEvent, false);
  DOM.addEventListener('drop', attachEvent, false);
};
export default {};
